.contact {
  padding: 10px;
  /* flex-direction: column; */
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background: linear-gradient(rgb(6 10 12 / 10%), rgb(12 18 27 / 40%)),
    url(/pratapprofile/static/media/pratap.8e61613f.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100vh;
  color: #fff;
  text-align: left;
  margin: auto;
}
.line {
  width: 450px;
  height: 1px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
}
.contact-list {
  padding: 10px;
  height: 35vh;
  text-align: left;
  margin-top: 30px;
}
.contact-list h1 {
  font-size: 30px;
  padding: 20px;
  /* border-bottom: 1px solid rgba(255, 255, 255, 0.5); */
}
.main-contact {
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
}
.contact-items {
  margin-top: 40px;
  padding: 20px;
}
.contact-items h3 {
  padding: 10px;
  font-size: 16px;
  font-weight: lighter;
  transition: 0.5s;
}
.contact-items h3:hover {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  padding: 10px;
  font-size: 16px;
  font-weight: lighter;
  margin-right: 30px;
  cursor: pointer;
}
.contact-items p {
  padding: 10px;
  font-size: 14px;
  transition: 0.5s;
}
.contact-items p:hover {
  padding: 10px;
  font-size: 14px;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  cursor: pointer;
}

* {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}
body {
  font-family: "Poppins", sans-serif;
}

section img {
  width: 100%;
}
section {
  background: linear-gradient(rgb(6 10 12 / 30%), rgb(12 18 27 / 60%)),
    url(/pratapprofile/static/media/pratapparis.a7132c0e.jpg);
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  height: 100vh;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}
section h3 {
  color: #fff;
  font-size: 48px;
}
section button {
  padding: 8px 25px;
  border: 1px solid #fff;
  width: 150px;
  margin-top: 10px;
  background-color: transparent;
  color: #fff;
  transition: 0.5s;
}
section button:hover {
  color: rgb(56, 200, 236);
  border: 1px solid rgb(56, 200, 236);
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

