  margin-top: 40px;
  text-align: center;
}
.cv-box img {
  width: 100px;
  transition: 0.5s;
}
.cv-box img:hover {
  width: 100px;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
.cv-box a {
  text-decoration: none;
  color: black;
  transition: 0.9s;
}
.cv-background {
